:root{
    --all-primary-backgound: #0A192F;
    --all-primary-font: #B8B4B3;
    --all-box-background: #0A192F;
    --all-box-font: #fff;   
    --all-box-border: 5px;
    --all-box-border-style: solid;
    --all-border-radius-round: 120px;
    --all-border-radius-oblong: 52px;
    --all-bottom-shadow: 10px;
    --all-box-shadow: 10px 10px 5px #darkslategray;
    --all-font: 'Verdana','Gill Sans', 'Gill Sans MT', 'Calibri', 'Trebuchet MS', 'sans-serif';
    --nav-border-color:#5Fc8FF;; 
}

* {    
    background: var(--all-primary-backgound);
    color: var(--all-primary-font);
    /* box-sizing: border-box; */
}

body{
  background: var(--all-primary-backgound);

}

h1 {
  
  color: rgb(106, 179,196)

}

.label {
  color: #0A192F;
}

h2{
  text-align: center;
  color: #5Fc8FF;
}

h3{
  text-align: center;
  color: #fecfec;
}

nav {
  color: #fecfec;
}

mark{
  background:rgba(253, 208, 4, 0.897);
  color:black;
}

.BannerBox{
  position: relative;
  justify-content: center;
  border-color: #0A192F;
}

.jumbotron{

    height: 100vh;
    background : var(--all-primary-backgound);
}

#catimg{

  mix-blend-mode: exclusion ;
  align-self: center;
  z-index: 1;

}

#heroimg 
   {
    -webkit-box-reflect: below 0px linear-gradient(to bottom, rgba(0,0,0,0.0), rgba(0,0,0,0.0));
    border-color: #0A192F;
    align-self: center
  };

button{

  color: var(--all-primary-font);
  display: inline-block;
  
}
#headimg{

  border-radius: var(--all-border-radius-oblong);
  mix-blend-mode: luminosity;
  position: sticky; 
  
  height:100px;
  width:100px;
}

nav  {
  transition: border-color 2s linear;
  border-color: var(--nav-border-color);
}

.i{
  color: rgb(106, 179,196);
  font-size: medium;
}


.resume{
  align-self: flex-end;
}
 
hr {
  height: 5px;
  color: #fbfed0; 
  border-width: 0;
  background-color: #fbfed0; 
}

a{
  align-content: center;
  background-color: var(--all-primary-background);
  color: #5Fc8FF;
  font-family: var(--all-font);
  font-size: 0.75em;
} 

.cert p {
color: #5Fc8FF;
}

footer p {

  color: var(--all-primary-font);
  background-color:var(--all-primary-backgound);
  text-align: center;
  font-size: medium;
}

#footer{  

  align-content: center;
  padding: 2%;
  background: var(--all-primary-backgound);
  color: var(--all-primary-font);
}
.credits {
  text-align: center;
  background-color: var(--all-primary-background);
  font-family: var(--all-font);
  font-size: 1em;
  color: #5Fc8FF;
}

#ContactDetails{
  background: var(--all-primary-backgound);
  color: var(--all-primary-font);
  font-size: 20px;
  font: small-caption;
  border-radius: var(--all-border-radius-oblong);
}

#img #phone #email #linkedin {
  padding: 10px;
}

@media screen and (max-width: 768px) {
  main{
  display: flex;
  flex-wrap: wrap;
  margin-top: 2%;
  }
}

main {
  display: block;
}

.responsive{
  width: 100%;
  height: auto;
  border-radius: 8px;
  border: 1px solid #fff;
  box-shadow: 0 10px 10px 5px #fefed0;
  }
.responsive:hover{
  box-shadow: 0 10px 10px 5px #d1fdf6;
  }

#ContactDetails p {

  margin: 0%;
  text-align: justify; 
  font-family: var(--all-font);
}
